'use client';

import {useState, useEffect} from 'react';
import {
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
} from '@/components/ui/table';
import {Button} from '@/components/ui/button';
import {Input} from '@/components/ui/input';
import {AlertDialog, AlertDialogTrigger, AlertDialogContent, AlertDialogHeader, AlertDialogTitle, AlertDialogDescription, AlertDialogFooter, AlertDialogAction, AlertDialogCancel} from '@/components/ui/alert-dialog';
import {useToast} from '@/hooks/use-toast';
import {Textarea} from '@/components/ui/textarea';
import {Card, CardHeader, CardTitle, CardDescription, CardContent} from '@/components/ui/card';
import {Separator} from '@/components/ui/separator';
import {Icons} from '@/components/icons';
import {auditDataForIndianNames} from '@/ai/flows/audit-data-for-indian-names';
import {provideDataAuditSummary} from '@/ai/flows/provide-data-audit-summary';
import {suggestDataCorrections} from '@/ai/flows/suggest-data-corrections';
import {Toaster} from "@/components/ui/toaster"
import {Avatar, AvatarImage, AvatarFallback} from "@/components/ui/avatar";
import {Label} from "@/components/ui/label";
import {
  getAuth,
  onAuthStateChanged,
  signInAnonymously,
  signOut,
} from 'firebase/auth';
import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  doc,
  deleteDoc,
} from 'firebase/firestore';
import {initializeApp} from 'firebase/app';

// Define a type for the data, each key is a string, and value can be a string or number
type Data = Record<string, string>;

export default function Home() {
  const [mounted, setMounted] = useState(false);
  const [data, setData] = useState<Data[]>([]);
  const [filename, setFilename] = useState<string>('');
  const [filesize, setFilesize] = useState<number>(0);
  const [modified, setModified] = useState<boolean>(false);
  const [auditResults, setAuditResults] = useState<string>('');
  const [auditSummary, setAuditSummary] = useState<string>('');
  const [dataCorrections, setDataCorrections] = useState<string>('');
  const [loadingAudit, setLoadingAudit] = useState<boolean>(false);
  const [user, setUser] = useState<any>(null);
  const [uploadedFiles, setUploadedFiles] = useState<any[]>([]);
  const [firebaseApp, setFirebaseApp] = useState<any>(null);
  const [firebaseConfig, setFirebaseConfig] = useState<any>(null);
  const [auth, setAuth] = useState<any>(null);
  const [db, setDb] = useState<any>(null);

  const {toast} = useToast();

  // Set mounted state to true after component mounts
  useEffect(() => {
    setMounted(true);
  }, []);

  // Initialize Firebase only after component mounts
  useEffect(() => {
    if (!mounted) return;

    const config = {
      apiKey: process.env.NEXT_PUBLIC_FIREBASE_API_KEY,
      authDomain: process.env.NEXT_PUBLIC_FIREBASE_AUTH_DOMAIN,
      projectId: process.env.NEXT_PUBLIC_FIREBASE_PROJECT_ID,
      storageBucket: process.env.NEXT_PUBLIC_FIREBASE_STORAGE_BUCKET,
      messagingSenderId: process.env.NEXT_PUBLIC_FIREBASE_MESSAGING_SENDER_ID,
      appId: process.env.NEXT_PUBLIC_FIREBASE_APP_ID,
    };
    setFirebaseConfig(config);
      
    if (!firebaseApp && config.apiKey) {
      try {
        const app = initializeApp(config);
        setFirebaseApp(app);
        const authInstance = getAuth(app);
        const dbInstance = getFirestore(app);
        setAuth(authInstance);
        setDb(dbInstance);
        console.log('Firebase initialized');
      } catch (error) {
        console.error('Error initializing Firebase:', error);
      }
    }
  }, [mounted]);

  // Set up auth state listener after Firebase is initialized
  useEffect(() => {
    if (!mounted || !auth) return;

    const unsubscribe = onAuthStateChanged(auth, async (authUser) => {
      if (authUser) {
        setUser(authUser);
        await loadFiles(authUser.uid);
      } else {
        setUser(null);
      }
    });

    return () => unsubscribe();
  }, [mounted, auth]);

  // Sign in anonymously after auth is initialized
  useEffect(() => {
    if (!mounted || !auth) return;

    signInAnonymously(auth)
      .then(() => {
        console.log('Signed in anonymously');
      })
      .catch((error) => {
        console.error('Error signing in anonymously:', error);
      });
  }, [mounted, auth]);

  const loadFiles = async (userId: string) => {
    if (!db) {
      console.error('Firestore not initialized');
      return;
    }
    try {
      const filesCollection = collection(db, `users/${userId}/files`);
      const filesSnapshot = await getDocs(filesCollection);
      const filesList = filesSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      }));
      setUploadedFiles(filesList);
    } catch (error: any) {
      toast({
        title: 'Error loading files',
        description: error.message,
        variant: 'destructive',
      });
      console.error('Error loading files:', error);
    }
  };

  const handleFileUpload = async (file: File) => {
    setFilename(file.name);
    setFilesize(file.size);

    const reader = new FileReader();

    reader.onload = async (e: ProgressEvent<FileReader>) => {
      const result = e.target?.result;
      if (typeof result === 'string') {
        try {
          // Upload file details to Firestore
          await uploadFileToFirestore(user.uid, file.name, file.size, result);
          toast({
            title: 'File uploaded successfully',
            description: `${file.name} has been uploaded.`,
          });
          await loadFiles(user.uid); // Reload files after upload

        } catch (error: any) {
          toast({
            title: 'Error uploading file',
            description: error.message,
            variant: 'destructive',
          });
          console.error('Error uploading file:', error);
        }
      }
    };
      reader.readAsText(file);
  };

  const uploadFileToFirestore = async (userId: string, fileName: string, fileSize: number, fileData: string) => {
    if (!db) {
      console.error('Firestore not initialized');
      return;
    }
    try {
      const filesCollection = collection(db, `users/${userId}/files`);
      await addDoc(filesCollection, {
        name: fileName,
        size: fileSize,
        data: fileData,
        uploadedAt: new Date(),
      });
    } catch (error: any) {
      console.error('Error uploading file to Firestore:', error);
      throw new Error('Failed to upload file to Firestore');
    }
  };

  const handleSignOut = () => {
    if (!auth) {
      console.error('Auth not initialized');
      return;
    }
    signOut(auth)
      .then(() => {
        console.log('Signed out successfully');
        setUser(null);
        setUploadedFiles([]);
        toast({
          title: 'Signed out',
          description: 'You have been signed out.',
        });
      })
      .catch((error) => {
        console.error('Error signing out:', error);
        toast({
          title: 'Sign out error',
          description: error.message,
          variant: 'destructive',
        });
      });
  };

  const handleCellChange = (index: number, key: string, value: string) => {
    setData(prevData => {
      const newData = [...prevData];
      newData[index][key] = value;
      setModified(true);
      return newData;
    });
  };

  const handleDeleteRow = (index: number) => {
    AlertDialog({children: 'Are you sure you want to delete this row?'});
    setData(prevData => {
      const newData = [...prevData];
      newData.splice(index, 1);
      setModified(true);
      return newData;
    });
  };

  const handleSend = () => {
    console.log('Modified Data:', data);
    setModified(false);
    toast({
      title: 'Data sent!',
      description: 'Modified data logged to console.',
    });
  };

  const handleAuditData = async () => {
    setLoadingAudit(true);
    try {
      // Convert data to the format expected by auditDataForIndianNames
      const auditInput = {data: data};
      const auditOutput = await auditDataForIndianNames(auditInput);
      setAuditResults(JSON.stringify(auditOutput, null, 2));

      // Get audit summary
      const summaryInput = {auditResults: JSON.stringify(auditOutput, null, 2)};
      const summaryOutput = await provideDataAuditSummary(summaryInput);
      setAuditSummary(summaryOutput.summary);

      // Get data corrections
      const dataString = JSON.stringify(data, null, 2);
      const correctionsInput = {data: dataString, auditResults: JSON.stringify(auditOutput, null, 2)};
      const correctionsOutput = await suggestDataCorrections(correctionsInput);
      setDataCorrections(correctionsOutput.suggestions);

    } catch (error: any) {
      toast({
        title: 'Error auditing data',
        description: error.message,
        variant: 'destructive',
      });
      setAuditResults('Error during audit.');
      setAuditSummary('Error during audit.');
      setDataCorrections('Error during audit.');
    } finally {
      setLoadingAudit(false);
    }
  };

  if (!mounted) {
    return null; // Return null on server-side to prevent hydration mismatch
  }

  return (
    <div className="min-h-screen bg-background text-foreground">
      <div className="container mx-auto p-4">
        {/* Profile Section */}
        <div className="flex justify-end items-center mb-4">
          {user ? (
            <div className="flex items-center">
              <Avatar className="mr-2">
                <AvatarImage src="https://picsum.photos/50/50" alt="Profile" />
                <AvatarFallback>JD</AvatarFallback>
              </Avatar>
              <span className="mr-2">User: {user.uid}</span>
              <Button variant="outline" onClick={handleSignOut}>
                Sign Out
              </Button>
            </div>
          ) : (
            <p>Please sign in</p>
          )}
        </div>
        <h1 className="text-2xl font-bold mb-4">DataWrangler</h1>

        {/* File Upload */}
        <Card>
          <CardHeader>
            <CardTitle>Upload Data</CardTitle>
            <CardDescription>Select a CSV file to upload</CardDescription>
          </CardHeader>
          <CardContent>
            <input
              type="file"
              accept=".csv"
              onChange={(e) => {
                const file = (e.target.files as FileList)[0];
                if (file) {
                  handleFileUpload(file);
                }
              }}
              className="dropzone border-2 border-dashed rounded-md p-4 cursor-pointer"
            />
            {filename && <p className="mt-2">Filename: {filename}, Size: {filesize} bytes</p>}
          </CardContent>
        </Card>

        <Separator className="my-4" />

        {/* Display Uploaded Files */}
        <Card>
          <CardHeader>
            <CardTitle>Uploaded Files</CardTitle>
            <CardDescription>View previously uploaded files</CardDescription>
          </CardHeader>
          <CardContent>
            {uploadedFiles.length > 0 ? (
              <ul>
                {uploadedFiles.map((file) => (
                  <li key={file.id}>
                    {file.name} - {file.size} bytes
                  </li>
                ))}
              </ul>
            ) : (
              <p>No files uploaded yet.</p>
            )}
          </CardContent>
        </Card>

        <Separator className="my-4" />

        {/* Data Display */}
        {data.length > 0 && (
          <Card>
            <CardHeader>
              <CardTitle>Data Display</CardTitle>
              <CardDescription>View, edit, and manipulate your data</CardDescription>
            </CardHeader>
            <CardContent className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    {Object.keys(data[0]).map(key => (
                      <TableHead key={key}>{key}</TableHead>
                    ))}
                    <TableHead className="text-right">Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {data.map((row, index) => (
                    <TableRow key={index}>
                      {Object.keys(row).map(key => (
                        <TableCell key={key}>
                          <Input
                            type="text"
                            value={String(row[key])}
                            onChange={(e) => handleCellChange(index, key, e.target.value)}
                          />
                        </TableCell>
                      ))}
                      <TableCell className="text-right">
                        <AlertDialog>
                          <AlertDialogTrigger asChild>
                            <Button variant="destructive" size="sm">
                              <Icons.trash className="h-4 w-4 mr-2" />
                              Delete
                            </Button>
                          </AlertDialogTrigger>
                          <AlertDialogContent>
                            <AlertDialogHeader>
                              <AlertDialogTitle>Are you absolutely sure?</AlertDialogTitle>
                              <AlertDialogDescription>This action cannot be undone.</AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                              <AlertDialogCancel>Cancel</AlertDialogCancel>
                              <AlertDialogAction onClick={() => handleDeleteRow(index)}>Continue</AlertDialogAction>
                            </AlertDialogFooter>
                          </AlertDialogContent>
                        </AlertDialog>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        )}

        <Separator className="my-4" />

        {/* AI Audit Section */}
        <Card>
          <CardHeader>
            <CardTitle>AI-Powered Data Audit</CardTitle>
            <CardDescription>Audit your data using AI to find potential issues</CardDescription>
          </CardHeader>
          <CardContent>
            <Button onClick={handleAuditData} disabled={loadingAudit}>
              {loadingAudit ? (
                <>
                  <Icons.spinner className="mr-2 h-4 w-4 animate-spin" />
                  Auditing...
                </>
              ) : (
                <>
                  <Icons.shield className="mr-2 h-4 w-4" />
                  Audit Data
                </>
              )}
            </Button>
            {auditResults ? (
              <div className="mt-4">
                <h3 className="text-lg font-semibold">Audit Results:</h3>
                <Textarea className="mt-2 w-full h-48" readOnly value={auditResults} />
              </div>
            ) : null}
            {auditSummary ? (
              <div className="mt-4">
                <h3 className="text-lg font-semibold">Audit Summary:</h3>
                <p className="mt-2">{auditSummary}</p>
              </div>
            ) : null}

            {dataCorrections ? (
              <div className="mt-4">
                <h3 className="text-lg font-semibold">Suggested Data Corrections:</h3>
                <Textarea className="mt-2 w-full h-48" readOnly value={dataCorrections} />
              </div>
            ) : null}
          </CardContent>
        </Card>

        <Separator className="my-4" />

        {/* Data Export */}
        {data.length > 0 && (
          <div className="mt-4">
            <Button onClick={handleSend} disabled={!modified}>
              Send Data
            </Button>
            {modified && <p className="mt-2 text-sm text-muted-foreground">Data has been modified.</p>}
          </div>
        )}
      </div>
      {/* Footer */}
      <footer className="text-center p-4 text-muted-foreground">
        Copyright  {new Date().getFullYear()} DataWrangler. All rights reserved.
      </footer>
      <Toaster />
    </div>
  );
}
